@charset "UTF-8";
body {
  font-family: "Noto Sans JP";
}

body {
  color: #fff;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.btn {
  display: inline-block;
  padding: 0.625rem 7.125rem;
  border: #fff;
  background-color: #333333;
  border: 0.0625rem solid #fff;
  font-size: 1.25rem;
  letter-spacing: 0.02em;
  position: relative;
}
@media screen and (min-width: 768px) {
  .btn {
    font-size: 1rem;
    padding: 0.1875rem 8.5rem;
    line-height: 3.375rem;
  }
}

.btn-text {
  width: 100%;
  height: 100%;
  z-index: 10;
}

.btn-text:before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  width: 0;
  display: block;
  background-color: #fff;
  z-index: -1;
  transition: 0.2s;
}

.btn-text:hover {
  color: #333333;
}

.btn-text:hover:before {
  width: 100%;
}

.drawer {
  width: 1.5rem;
  position: relative;
  z-index: 300;
}
.drawer.open .drawer__icon-bar1 {
  transform: rotate(-45deg);
  top: 0.5rem;
  background: #fff;
}
.drawer.open .drawer__icon-bar2 {
  display: none;
}
.drawer.open .drawer__icon-bar3 {
  transform: rotate(45deg);
  top: 0.5rem;
  background: #fff;
}

.drawer__icon {
  width: 1.5rem;
  height: 1.5rem;
  position: relative;
  margin-right: 0.75rem;
}

.drawer__icon-bar1,
.drawer__icon-bar2,
.drawer__icon-bar3 {
  position: absolute;
  width: 1.5rem;
  height: 0.125rem;
  background: #fff;
  border-radius: 2px;
  top: 0;
  left: 0;
}

.drawer__icon-bar1 {
  top: 0;
}

.drawer__icon-bar2 {
  top: 0.5rem;
}

.drawer__icon-bar3 {
  top: 1rem;
}

.footer {
  background-color: #111;
}

.footer__inner {
  padding: 3rem 0;
}

.header {
  height: 3.125rem;
  background-color: #111;
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: inherit;
  padding-left: 1.375rem;
  padding-right: 1.375rem;
}
@media screen and (min-width: 768px) {
  .header__inner {
    padding-left: 2rem;
    padding-right: 0;
  }
}

.inner {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1150px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.logo {
  width: 4.6875rem;
  position: relative;
  z-index: 300;
  padding-top: 0.5rem;
}
@media screen and (min-width: 768px) {
  .logo {
    width: 5.875rem;
  }
}

.pc-nav {
  height: inherit;
}

.pc-nav__items {
  display: flex;
  height: inherit;
  align-items: center;
}

.pc-nav__item {
  height: inherit;
  padding: 0 1rem;
  position: relative;
}

.pc-nav__item a {
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
}
.pc-nav__item a::after {
  position: absolute;
  content: "";
  width: 4.375rem;
  height: 0.0625rem;
  bottom: 0.5rem;
  background-color: #FFEA2E;
  transform: scale(0, 1); /*アンダーラインの縮尺比率。ホバー前はx方向に0*/
  transform-origin: left top; /*変形（アンダーラインの伸長）の原点がaタグ（各メニュー）の左端*/
  transition: transform 0.3s; /*変形の時間*/
}
.pc-nav__item a:hover::after {
  transform: scale(1, 1); /*ホバー後、x軸方向に1（相対値）伸長*/
}

.pc-nav__item--white {
  padding-right: 2rem;
  background-color: #fff;
}

.pc-nav__item--white a {
  color: #333;
}

.sp-nav {
  background: #111;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 299;
  transform: translateX(105%);
}
.sp-nav.open {
  transform: translateX(0);
}

.sp-nav__items {
  padding: 13.125rem 0;
  color: #fff;
  font-size: 1rem;
}

.sp-nav__item {
  display: flex;
  position: relative;
  justify-content: center;
  letter-spacing: 0.1em;
}
.sp-nav__item:not(:first-child) {
  margin-top: 1.9375rem;
}

.test {
  font-size: 1.25rem;
  background-color: #111;
  color: #fff;
}

.pagetop {
  max-width: 2.8125rem;
  width: 100%;
}